import React, { Component } from 'react';
import './styles.css';

class Main extends Component {
  render() {
    return (
      <main className="col-12 col-md-8 order-md-2 b-black align-items-stretch px-0"> 
        <div className="row mx-0">
          <div className="col-8 d-flex flex-row px-0">
            <div id="carouselExampleIndicators" className="carousel slide w-100" data-ride="carousel" data-interval="3000">
              <ol className="carousel-indicators ci-position">
                <li data-target="#carouselExampleIndicators" data-slide-to="0" className="active"></li>
                <li data-target="#carouselExampleIndicators" data-slide-to="1"></li>
                <li data-target="#carouselExampleIndicators" data-slide-to="2"></li>
              </ol>
              <div className="carousel-inner">
                <div className="carousel-item active">
                  <img className="d-block w-100" src="https://image.tmdb.org/t/p/w500/pk1zexoUCtjFs761W48QyIPzwCj.jpg" alt="First slide" />
                  <div className="bblock"></div>
                  <div className="carousel-caption d-none d-md-block cc-position">
                    <h5>Film 1</h5>
                    <p>good film</p>
                  </div>
                </div>
                <div className="carousel-item">
                  <img className="d-block w-100" src="https://image.tmdb.org/t/p/w500/5KhMpFJdnOAsDmIpT0TINUNDt8L.jpg" alt="Second slide" />
                  <div className="bblock"></div>
                  <div className="carousel-caption d-none d-md-block cc-position">
                    <h5>Film 2</h5>
                    <p>so-so dfsdfadfdaf a afadfafasfadfa asfasfadfafasfas asfasfasfafas asfasfasfas</p>
                  </div>
                </div>
                <div className="carousel-item">
                  <img className="d-block w-100" src="https://image.tmdb.org/t/p/w500/ecHD0rmx8l4eZVSISLJVAgAaODU.jpg" alt="Third slide" />
                  <div className="bblock"></div>
                  <div className="carousel-caption d-none d-md-block cc-position">
                    <h5>Film 3</h5>
                    <p>best one</p>
                  </div>
                </div>
              </div>
              <a className="carousel-control-prev" href="#carouselExampleIndicators" role="button" data-slide="prev">
                <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                <span className="sr-only">Previous</span>
              </a>
              <a className="carousel-control-next" href="#carouselExampleIndicators" role="button" data-slide="next">
                <span className="carousel-control-next-icon" aria-hidden="true"></span>
                <span className="sr-only">Next</span>
              </a>
            </div>
          </div>
          <div className="col-4 d-flex flex-column px-0">        
            <img className="d-block w-100" style={{height: "50%"}} src="https://image.tmdb.org/t/p/w500/pk1zexoUCtjFs761W48QyIPzwCj.jpg" alt="Second slide" />
            <img className="d-block w-100" style={{height: "50%"}} src="https://image.tmdb.org/t/p/w500/5KhMpFJdnOAsDmIpT0TINUNDt8L.jpg" alt="Second slide" />
          </div>
        </div>
        <div className="col-12 d-flex flex-column px-0 publications">
          <div className="col-12 d-flex justify-content-center align-items-center px-0 py-2 publications-title">
            <h5>Новости</h5>
          </div>
          <div className="col-12 d-flex justify-content-around p-1 publicationsList">
            <div className="card px-0 mx-1">
              <img className="card-img-top" src="https://image.tmdb.org/t/p/w500/5KhMpFJdnOAsDmIpT0TINUNDt8L.jpg" alt="Card image cap" />
              <div className="card-body">
                <h5 className="card-title">Card title</h5>
                <p className="card-text">This is a wider card with supporting text below as a natural lead-in to additional content. This content is a little bit longer.</p>
              </div>
              <div className="card-footer">
                <small className="text-muted">Last updated 3 mins ago</small>
              </div>
            </div>
            <div className="card px-0 mx-1">
              <img className="card-img-top" src="https://image.tmdb.org/t/p/w500/ecHD0rmx8l4eZVSISLJVAgAaODU.jpg" alt="Card image cap" />
              <div className="card-body">
                <h5 className="card-title">Card title</h5>
                <p className="card-text">This card has supporting text below as a natural lead-in to additional content.</p>
              </div>
              <div className="card-footer">
                <small className="text-muted">Last updated 3 mins ago</small>
              </div>
            </div>
            <div className="card px-0 mx-1">
              <img className="card-img-top" src="https://image.tmdb.org/t/p/w500/pk1zexoUCtjFs761W48QyIPzwCj.jpg" alt="Card image cap" />
              <div className="card-body">
                <h5 className="card-title">Card title</h5>
                <p className="card-text">This is a wider card with supporting text below as a natural lead-in to additional content. This card has even longer content than the first to show that equal height action.</p>
              </div>
              <div className="card-footer">
                <small className="text-muted">Last updated 3 mins ago</small>
              </div>
            </div>
          </div>
          <div className="d-flex justify-content-center m-2 mb-1 publicationsListLink">
            <a href="#">все новости</a>
          </div>
        </div>
      </main>
    )
  }
}

export default Main;