import React, { Component } from 'react';
import './styles.css';
import logo from '../../assets/img/logo/logo-odessa.png';

class Partners extends Component {
    render() {
      return (
        <div className="col-12 d-flex partners px-0">
          <div className="d-none d-md-flex col-md-2 justify-content-center align-items-center p-1 b-red header--adv-logo">        
            <img style={{opacity: "0.7"}} width="100" src={logo} alt="zdvyzh" />
          </div>
          <div className="col-12 col-md-10 b-red d-flex justify-content-around align-items-center header--adv-partners">
            <div className="col-12 col-md-10 d-flex">
              <div className="col-2 px-1 py-2">
                <img src={logo} className="partner-logo" style={{}} alt="name" />
              </div>
              <div className="col-2 px-1 py-2">
                <img src={logo} className="partner-logo" alt="name" />
              </div>
              <div className="col-2 px-1 py-2">
                <img src={logo} className="partner-logo" alt="name" />
              </div>
              <div className="col-2 px-1 py-2">
                <img src={logo} className="partner-logo" alt="name" />
              </div>
              <div className="col-2 px-1 py-2">
                <img src={logo} className="partner-logo" alt="name" />
              </div>
              <div className="col-2 px-1 py-2">
                <img src={logo} className="partner-logo" alt="name" />
              </div>
            </div>    
          </div>
        </div>
      )
    }
  }

  export default Partners;