import React, { Component } from 'react';
import './styles.css';
import firstPhoto from '../../assets/img/players/dimitrov.png';
import secondPhoto from '../../assets/img/players/bugaiov.png';
import thirdPhoto from '../../assets/img/players/savastru.png';
import fourthPhoto from '../../assets/img/players/kirichenko.png';
import fifthPhoto from '../../assets/img/players/kovalyov.png';

import PhotoList from '../photoList/photoList.js';
import VideoList from '../videoList/videoList.js';

class Sidebar extends Component {
  render() {
    return (
      <aside className="col-12 col-md-4 order-md-1 px-0 align-items-center b-black">
        <PhotoList 
          quantity={4}
          photoData={[{ id: 1, name: 'firstphoto', image: firstPhoto},
                      { id: 2, name: 'secondphoto', image:  secondPhoto},
                      { id: 3, name: 'thirdphoto', image: thirdPhoto },
                      { id: 4, name: 'fourthphoto', image:  fourthPhoto},
                      { id: 5, name: 'fifthphoto', image:  fifthPhoto}]}
        />
        <VideoList 
          quantity={2}
          videoData={[{ id: 6, name: 'firstvideo', video: 'https://www.youtube.com/embed/krPnpKGykQs'},
                      { id: 7, name: 'secondvideo', video:  'https://www.youtube.com/embed/u5CcInM4EYU'},
                      { id: 8, name: 'thirdvideo', video: 'https://www.youtube.com/embed/krPnpKGykQs' },
                      { id: 9, name: 'fourthvideo', video:  'https://www.youtube.com/embed/u5CcInM4EYU'},
                      { id: 10, name: 'fifthvideo', video:  'https://www.youtube.com/embed/krPnpKGykQs'}]}
        />
      </aside>
    )
  }
}

export default Sidebar;